body {
  font-family: "Open Sans", sans-serif;
  background: linear-gradient(45deg, #787492 0%, #545166 100%); }

h2 {
  text-align: center;
  font-weight: 100; }

.page-wrapper {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center; }

.calendar-phase-wrapper {
  width: 70%; }

.calendar-container, .phase-container {
  transition: border-radius .2s ease-in-out;
  width: 50%;
  display: block;
  box-sizing: border-box;
  padding: 20px;
  min-height: 485px; }

.calendar-container {
  float: left;
  border-radius: 15px 0 0 15px;
  color: #444;
  background-color: #fff; }

.phase-container {
  float: right;
  height: 100%;
  border-radius: 0 15px 15px 0;
  background: linear-gradient(45deg, #7a7996 0%, #b9bde4 100%);
  color: #fff; }

/* Days of the week */
ul.calendar-weekdays {
  margin: 0;
  padding: 0; }
  ul.calendar-weekdays li.week-day {
    display: inline-block;
    width: calc(100% / 7);
    margin: 0;
    padding: 0; }

li.emptySlot, li.day {
  width: calc(100% / 7);
  display: inline-block;
  list-style: none;
  list-style-type: none;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
  transition: all .3s ease-in-out; }
  li.emptySlot span, li.day span {
    position: absolute;
    top: 10px;
    left: 10px; }
  li.emptySlot.selected-day, li.day.selected-day {
    background-color: yellow;
    width: 28px;
    height: 28px;
    line-height: 28px;
    border-radius: 2%;
    color: black; }
  li.emptySlot.current-day, li.day.current-day {
    background-color: #787CBB;
    color: #fff; }

li.day:hover {
  background-color: #eee; }

.week-day {
  color: #666;
  text-align: center;
  display: inline-block;
  background-color: #eee; }

.moonMeta {
  display: none; }

.moonPhase {
  text-align: center; }

.day {
  cursor: pointer;
  width: 24px;
  height: 24px;
  line-height: 24px; }

.month-popup {
  position: absolute;
  padding: 5px;
  background: white;
  border: 2px solid skyblue; }

.month-popup div {
  padding: 0.3em; }

.nav-month {
  position: absolute;
  top: 0.6em;
  font-size: 0.6em;
  right: 3px; }

.editor-year {
  max-width: 3.6em; }

.label-month-nav {
  font-size: 0.6em;
  cursor: pointer;
  display: none; }

/* Moon Phase */
ul.moonPhase-prompts {
  list-style: none;
  margin: 20px auto 0 auto;
  padding: 0px;
  text-align: left;
  width: 80%; }

ul.moonPhase-prompts li {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 5px 15px;
  display: block;
  transition: all .4s ease-in-out;
  padding-left: 3px; }

ul.moonPhase-prompts li:hover {
  opacity: 0.5;
  padding-left: 8px; }
